import './pagination.css';
import '../reset.css';

export default class Pagination {
  static draw() {
    const pagination = document.createElement('section');
    pagination.className = 'pagination';
    document.body.appendChild(pagination);

    const list = document.createElement('ul');
    list.className = 'paginationList';
    pagination.appendChild(list);
  }

  static paginationClear() {
    while (document.querySelectorAll('.listItem').length > 0) {
      document.querySelector('.listItem').remove();
    }
  }

  static drawOnePage() {
    const list = document.querySelector('.paginationList');

    const tooltip = document.createElement('span');
    tooltip.className = 'tooltip';

    const listItem = document.createElement('li');
    listItem.className = 'listItem paginationNumber';
    list.appendChild(listItem);

    const listItemTwo = document.createElement('li');
    listItemTwo.className = 'listItem next';
    list.appendChild(listItemTwo);
    listItemTwo.appendChild(tooltip);
  }

  static drawTwoPage() {
    const list = document.querySelector('.paginationList');

    const tooltip = document.createElement('span');
    tooltip.className = 'tooltip';

    const tooltipTwo = document.createElement('span');
    tooltipTwo.className = 'tooltip';

    const listItem = document.createElement('li');
    listItem.className = 'listItem prev';
    list.appendChild(listItem);
    listItem.appendChild(tooltip);

    const listItemTwo = document.createElement('li');
    listItemTwo.className = 'listItem paginationNumber';
    list.appendChild(listItemTwo);

    const listItemThree = document.createElement('li');
    listItemThree.className = 'listItem next';
    list.appendChild(listItemThree);
    listItemThree.appendChild(tooltipTwo);
  }

  static drawThreePage() {
    const list = document.querySelector('.paginationList');

    const tooltip = document.createElement('span');
    tooltip.className = 'tooltip';

    const tooltipTwo = document.createElement('span');
    tooltipTwo.className = 'tooltip';

    const tooltipThree = document.createElement('span');
    tooltipThree.className = 'tooltip';

    const listItem = document.createElement('li');
    listItem.className = 'listItem prevprev';
    list.appendChild(listItem);
    listItem.appendChild(tooltip);

    const listItemTwo = document.createElement('li');
    listItemTwo.className = 'listItem prev';
    list.appendChild(listItemTwo);
    listItemTwo.appendChild(tooltipTwo);

    const listItemThree = document.createElement('li');
    listItemThree.className = 'listItem paginationNumber';
    list.appendChild(listItemThree);

    const listItemFour = document.createElement('li');
    listItemFour.className = 'listItem next';
    list.appendChild(listItemFour);
    listItemFour.appendChild(tooltipThree);
  }

  static drawPaginationItem(currentPage) {
    if (currentPage === 1) {
      Pagination.paginationClear();
      Pagination.drawOnePage();
    } else if (currentPage === 2) {
      Pagination.paginationClear();
      Pagination.drawTwoPage();
      document.querySelector('.listItem:first-child .tooltip').textContent = currentPage - 1;
    } else if (currentPage === 3) {
      Pagination.paginationClear();
      Pagination.drawThreePage();
      document.querySelector('.listItem:first-child .tooltip').textContent = currentPage - 2;
      document.querySelector('.listItem:nth-child(2) .tooltip').textContent = currentPage - 1;
    } else {
      document.querySelector('.listItem:first-child .tooltip').textContent = currentPage - 2;
      document.querySelector('.listItem:nth-child(2) .tooltip').textContent = currentPage - 1;
    }

    document.querySelector('.listItem:last-child .tooltip').textContent = currentPage + 1;

    document.querySelector('.paginationNumber').textContent = `${currentPage}`;
  }
}
